/****
    * Mixins - Media Queries
    * I have set several mixins for media queries,
    * so that you can just using this to achieve quick coding.
    */

/* @media (max-width: 543px) */
@mixin media-max-xs {
    @media (max-width: #{$screen-xs - 1px}) {
        @content; } }

/* @media (min-width: 544px) */
@mixin media-min-xs {
    @media (min-width: #{$screen-xs}) {
        @content; } }

/* @media (max-width: 767px) */
@mixin media-max-sm {
    @media (max-width: #{$screen-sm - 1px}) {
        @content; } }

/* @media (min-width: 768px) */
@mixin media-min-sm {
    @media (min-width: #{$screen-sm}) {
        @content; } }

/* @media (min-width: 768px) and (max-width: 991px) */
@mixin media-sm-md {
    @media (min-width: $screen-sm) and (max-width: #{$screen-md - 1px}) {
        @content; } }

/* @media (min-width: 992px) */
@mixin media-min-md {
    @media (min-width: #{$screen-md}) {
        @content; } }

/* @media (max-width: 992px) */
@mixin media-max-md {
    @media (max-width: #{$screen-md - 1px}) {
        @content; } }

/* @media (min-width: 1200px) */
@mixin media-min-lg {
    @media (min-width: #{$screen-lg}) {
        @content; } }

/* @media (min-width: customize) */
@mixin media-min-size($size) {
    @media (min-width: $size) {
        @content; } }

/* @media (max-width: customize) */
@mixin media-max-size($size) {
    @media (max-width: $size) {
        @content; } }

/* Hide the element start from $screen-md or below (<=992px) */
.desktop-only {
    @include media-max-md {
        display: none !important; } }

/* Hide the element start from $screen-md or above (>=992px) */
.desktop-hide {
    @include media-min-md {
        display: none !important; } }

/* Hide the element below $screen-sm and above $screen-md (<=768px && >=992px) */
.tablet-only {
    @include media-max-sm {
        display: none !important; }
    @include media-min-md {
        display: none !important; } }

/* Hide the element between $screen-sm and above $screen-md (<=768px && >=992px) */
.tablet-hide {
    @include media-sm-md {
        display: none !important; } }

/* Hide the element start from $screen-sm or above (>=768px) */
.mobile-only {
    @include media-min-sm {
        display: none !important; } }

/* Hide the element start from $screen-sm or below (<=768px) */
.mobile-hide {
    @include media-max-sm {
        display: none !important; } }
