/* Media breakpoints via bootstrap v3.0 */
$screen-xs: 554px /* Breakpoints between 340px and 768px */;
$screen-sm: 768px;
$screen-md: 992px;
$screen-lg: 1200px;

$shadow-color: #999;

/* Number of columns in the grid system (default use bootstrap number) */
$grid-columns: 12 !default;
/* Padding between columns. Gets divided in half for the left and right */
$grid-gutter-width: 30px !default;

/* Font family */
$font-family: proxima-nova, "Helvetica Neue", Helvetica, "Microsoft JhengHei", "Open Sans", Arial, sans-serif;
